<template>
	<el-container class="wrap">
		<el-header style="height: auto"><app-header></app-header></el-header>
		<el-container>
		  	<el-aside>
		  		<admin-aside></admin-aside>
		  	</el-aside>
		 	<el-main class="content">
			    <router-view></router-view>			   	
		 	</el-main>
		</el-container>
		<el-footer style="height: auto"><app-footer></app-footer></el-footer>
  </el-container>
</template>

<script>
import appHeader from '@/views/public/Header';
import appFooter from '@/views/public/Footer';
import adminAside from '@/views/admin/Aside'
export default {
  name: "TheAdmin",
  components: {appHeader, appFooter, adminAside},
};
</script>

<style lang="scss" scoped>
	@import '@/assets/public/css/box.scss';
	.el-header{
		@include main-header;
	}
	.el-main {
		@include main-body;
	}
	.el-aside{
		@include main-aside;
		margin-left: 0px;
	}
	.el-footer{
		@include main-footer;
	}
</style>



