<template>
	<el-menu :default-active="$route.path" @open="handleOpen" @close="handleClose" router>
		<h5>还没名字网</h5>
		<aside-item  v-for="route in permission_routers" :key="route.path" :item="route" class="tal"></aside-item>
	</el-menu>
</template>

<script>
import asideItem from '@/views/public/AsideItem';
import { mapGetters } from 'vuex'
export default {
	name:'Aside',
	components:{ asideItem },
	computed: {
    	...mapGetters([
      		'permission_routers'
    	])
  	},
	methods: {
		handleOpen(key, keyPath) {
			console.log(this.permission_routers)
			console.log(key, keyPath);
		},
		handleClose(key, keyPath) {
			console.log(key, keyPath);
		}
	},
}
</script>

<style>

